<template>
  <div class="admin-list">
    <el-dialog title="" :visible.sync="dialogAdminList" width="780px">
      <div class="content">
        <div class="search">
          <input type="text" placeholder="" class="center">
          <button class="blur-btn"><i class="icon-m icon-search-2"></i></button>
        </div>
        <template>
          <el-table
            ref="multipleTable"
            :data="adminList"
            border
            style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column property="name" label="" width="90"></el-table-column>
            <el-table-column property="mobile" label="" width="120"></el-table-column>
            <el-table-column property="position" label="" width="80"></el-table-column>
            <el-table-column property="companyName" label="" show-overflow-tooltip></el-table-column>
            <el-table-column property="role" label="" width="110"></el-table-column>
            <el-table-column label="" width="100">
                <template slot-scope="scope">
                <el-button size="mini" type="success" @click="handleEdit(scope.$index, scope.row)"><i class="icon-s icon-success"></i></el-button>
                </template>
            </el-table-column>
          </el-table>         
        </template>
      </div>
    </el-dialog>
  </div>
</template>

