import axios from 'axios'
import { getAuthToken } from './session'
import { Notification,Message,Loading } from 'element-ui'
import router from '@/router/router'

// 
let networkError = false
const warn = (message) => {
  console.log(message)
  if (!networkError) {
    networkError = true
    Notification.error({
      message,
      onClose () {
        networkError = false
      }
    })
  }
}

export const baseURL = process.env.CONNECTOR_SERVER
const api = axios.create({
  baseURL,
  timeout: 20000,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
    // 'Content-Type': 'application/json;charset=UTF-8'
  },
  showLoading:true
})
let needLoadingRequestCount = 0
export function showFullScreenLoading() {
 if (needLoadingRequestCount === 0) {
  startLoading()
 }
 needLoadingRequestCount++
}
export function tryHideFullScreenLoading() {
 if (needLoadingRequestCount <= 0) return
 needLoadingRequestCount--
 if (needLoadingRequestCount === 0) {
  endLoading()
 }
}
let loading
function startLoading() {
 loading = Loading.service({
  lock: true,
  text: '......',
  background: 'rgba(0, 0, 0, 0.7)'
 })
}
function endLoading() {
 loading.close()
}
// 
api.interceptors.request.use((config) => {
  // token
  const authToken = getAuthToken()
  if (authToken) {
    config.headers['Authorization'] = authToken
  }
  if (config.showLoading) {
    showFullScreenLoading()
   }
  // 
  for (const i in config.params) {
    if (config.params[i] === undefined ||
      config.params[i] === null ||
      config.params[i] === '') {
      delete config.params[i]
    }
  }
  return config
})

// 
api.interceptors.response.use(response => {
  if (response.config.showLoading) {
    tryHideFullScreenLoading()
   }
 if(response.data.code === 401){
  Message.error(response.data.message)
  setTimeout(()=>{
   router.push({ path: '/login' })
  },1000)
  return false
 }
 return response
}, (err) => {
  console.log(err)
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        err.message = ''
        break

      case 401:
        err.message = ''
        router.push({ path: '/login' })
        break

      case 403:
        err.message = ''
        break

      case 404:
        err.message = `: ${err.response.config.url}`
        break

      case 408:
        err.message = ''
        break

      case 500:
        err.message = ''
        break

      case 501:
        err.message = ''
        break

      case 502:
        err.message = ''
        break

      case 503:
        err.message = ''
        break

      case 504:
        err.message = ''
        break

      case 505:
        err.message = 'HTTP'
        break

      default:
    }
  } else {
    err.message = ''
  }
  if (err && err.response && err.response.data && err.response.data.message) {
    warn(err.response.data.message)
  } else {
    warn(err.message)
  }
  return Promise.reject(err)
})

// export default api
export default {
  install: function (Vue, option = {}) {
    // 1. Vue.httpClient 
    Vue.httpClient = api
    // 2. this.$httpClient 
    Vue.prototype.$httpClient = api
  }
}



